<header className="sticky top-0 z-50 w-full border-b border-border/40 bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 shadow-2xl">
  <div className="container flex h-14 items-center justify-between px-6 sm:px-10 md:px-20 lg:px-40">
    {/* Left: Name / Logo */}
    <Link href="/" className="font-bold text-lg sm:text-lg md:text-lg lg:text-lg">
      Randel Yumul
    </Link>

    {/* Right: Navigation + Theme Toggle + Mobile Menu */}
    <div className="flex items-center space-x-2 sm:space-x-3 md:space-x-4">
      {/* Desktop Navigation */}
      <nav className="hidden md:flex items-center space-x-4 lg:space-x-6 text-sm font-medium">
        {navLinks.map(({ href, label }) => (
          <Link
            key={href}
            href={href}
            className="transition-colors hover:text-[#1da1f2] text-black dark:hover:text-[#1da1f2] dark:text-white"
          >
            {label}
          </Link>
        ))}
      </nav>

      {/* Theme Toggle */}
      <ThemeToggle />

      {/* Mobile Menu */}
      <Sheet>
        <SheetTrigger asChild>
          <Button variant="ghost" className="md:hidden" size="icon" aria-label="Open menu">
            <Menu className="h-5 w-5 sm:h-6 sm:w-6" />
          </Button>
        </SheetTrigger>
        <SheetContent side="right" className="w-[240px]">
          <div className="flex flex-col h-full w-full items-center text-center">
            <div className="flex items-center mb-8">
              <span className="font-bold text-lg">Randel Yumul</span>
            </div>
            <nav className="flex flex-col space-y-4">
              {navLinks.map(({ href, label }) => (
                <Link
                  key={href}
                  href={href}
                  className="text-foreground/80 hover:text-foreground transition-colors"
                >
                  {label}
                </Link>
              ))}
            </nav>
          </div>
        </SheetContent>
      </Sheet>
    </div>
  </div>
</header>
